body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-53xl: 72px;

  /* Colors */
  --color-darkslategray: #2f2f2f;
  --color-white: #fff;
  --color-gray: #232323;
  --color-mediumturquoise: #6bdfda;
  --color-skyblue: #9ad7f3;
  --color-pink: #fec3c7;

  /* Gaps */
  --gap-lg: 18px;
  --gap-5xs: 8px;
  --gap-5xl: 24px;

  /* Paddings */
  --padding-5xl: 24px;
  --padding-29xl: 48px;

  /* Border radiuses */
  --br-21xl: 40px;
  --br-19xl: 38px;
}
